<template>
    <div id="app">
        <!-- 左侧 -->
        <div class="sidebar">
            <ul class="list">
                <li @click="tofirst">
                    <i class="fa fa-home" aria-hidden="true" />首页
                </li>
                <li @click="tocar" v-if="userType == 'ADMIN'">
                    <i class="fa fa-car" aria-hidden="true" />汽车租赁管理
                </li>
                <li @click="touser" v-if="userType == 'ADMIN'">
                    <i class="fa fa-user" aria-hidden="true" />用户管理
                </li>
                <li @click="toCarRenter" v-if="userType == 'NORMAL'">
                    <i class="fa fa-car" aria-hidden="true" />汽车租赁
                </li>
            </ul>
        </div>
        <!-- 右侧 -->
        <div class="main-content">
            <div class="top">
                <div class="right">
                    <div class="name">{{ userName }}</div>
                    <div class="identity">{{ userType == 'ADMIN' ? '管理员' : '用户' }}</div>
                    <i class="fa fa-angle-down" aria-hidden="true" @click="toggleMenu" />
                    <div v-if="showMenu" class="menu">
                        <div class="menu-item" @click="toLogin()">退出登录</div>
                    </div>
                </div>
            </div>
            <!-- 其他内容 -->
            <div class="neirong">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return {
            showMenu: false,
            userName: "用户名",
            userType: '',
            data: {}
        };
    },
    computed: {

    },
    created() {
        this.userName = localStorage.getItem('userName');
        this.getUserType(this.userName)
        this.getUserID(this.userName)
    },
    methods: {
        getUserID(userName) {
            let url = `/car/getUserID/${userName}`;
            axios.get(url)
                .then(response => {
                    localStorage.setItem("userID", response.data);
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
        },
        getUserType(userName) {
            let url = `/car/getUserType/${userName}`;

            axios.get(url)
                .then(response => {
                    this.userType = response.data
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
        },
        toLogin() {
            //清除缓存
            localStorage.setItem('userName', null);
            localStorage.setItem('userType', null);
            //回到登录页面
            this.$router.push("/");
        },
        toggleMenu() {
            this.showMenu = !this.showMenu;
            console.log("user:" + this.userName + ",userType:" + this.userType)
        },
        tofirst() {
            //to home
            console.log(this.userName)
            console.log(this.userType)

            this.$router.push("/main/home");
        },
        tocar() {
            //to carRenter
            this.$router.push("/main/carManage");

        },
        touser() {
            this.$router.push("/main/userManage");
        },
        toCarRenter() {
            this.$router.push("/main/carRenter");
        }
    },
};
</script>

<style scoped>
#app {
    display: flex;
    height: 100%;
}

/* 左侧列表 */
.list {
    padding: 0;
    background-color: #d8e5ff;
}

.sidebar {
    width: 40rem;
    height: 100%;
    background-color: #d8e5ff;
}

.sidebar li {
    width: 100%;
    height: 4vw;
    background-color: #93b7ff;
    margin-bottom: 0.12vw;
    font-size: 1.4vw;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.sidebar i {
    margin-left: 1.3vw;
    margin-right: 0.3vw;
}

/* 右侧顶部 */
.main-content {
    flex-grow: 1;
    background-color: rgb(255, 255, 255);
}

.main-content .top {
    height: 4vw;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: right;
    box-shadow: 0px 4px 3px -1px gray;
    background-color: #fff;
}

/* 用户信息 */
.main-content .top .right {
    display: flex;
    width: 12vw;
}

.main-content .top .right .name {
    margin-right: 1vw;
    font-size: 1.2vw;
}

.main-content .top .right .identity {
    margin-right: 1vw;
    font-size: 1.2vw;
    width: 50vw;
    text-align: center;
    border-radius: 3.5px;
    color: white;
    background-color: #9fc5ff;
}

.main-content .top .right i {
    font-size: 1.6vw;
}

/* 点击图标时弹窗 */
.main-content .top .right .menu {
    margin-top: 2vw;
    margin-left: 1vw;
    font-size: 1.2vw;
    position: absolute;
    background-color: white;
    border: 1px solid #ccc;
    padding: 15px 32px 15px 32px;
    width: 74px;
    border-radius: 9.5px;
}

.menu-item:hover {
    color: red;
    cursor: pointer;
}

/*  */
.main-content .neirong {
    height: 43vw;
    width: 84vw;
    margin-top: 1vw;
    margin-left: 1.5vw;
    background-color: #fff;
}
</style>